<template>
	<div id="more-password">
		<public-header title="修改密码"></public-header>
		<div class="content">
			<van-cell-group>
				<van-field v-model="password" type="password" placeholder="当前密码" />
				<van-field v-model="newpassword" type="password" placeholder="新密码" />
				<van-field v-model="repassword" type="password" placeholder="确认密码" />
			</van-cell-group>
			<div class="padding-15" @click="onSubmit()">
				<van-button size="normal" class="bg-info font-16">确定</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				password: '',
				newpassword: '',
				repassword: '',
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onSubmit() {
				var that = this;
				setData({
					vue: this,
					url: 'manage/more/profile/password',
					data: {
						'password': that.password,
						'newpassword': that.newpassword,
						'repassword': that.repassword
					},
				});
			}
		},
		mounted(){
			this.showPreLoading = false;
		}
	}
</script>

<style>
	#more-password .content .van-cell-group{
		margin-top: 15px;
	}
	#more-password .content .van-button{
		width: 100%;
	}
</style>


